import { howToApplyConfig } from './patterns/how-to-apply/pgt-data';
import edn from 'edn-to-js';

document.addEventListener('DOMContentLoaded', () => {
    if (/(Trident|MSIE)/.test(navigator.userAgent)) {
        return;
    } else {
        const howToApplyCTAs = document.querySelectorAll(
            '.how-to-apply--pgt.how-to-apply--pgt--js'
        );
        howToApplyCTAs &&
            Array.from(howToApplyCTAs).forEach(howToApply => {
                howToApplyConfig(howToApply.dataset.config).then(config => {
                    console.log(edn(config));
                    // render(
                    //     <HowToApply
                    //         config={edn(config)}
                    //         element={howToApply}
                    //     />,
                    //     howToApply
                    // );
                });
            });
    }
});
